import { Link } from "react-router-dom"
import reactLogo from "../assets/images/logo192.png"
import nodeLogo from "../assets/images/node.png"
import teardownLogo from "../assets/images/td.png"

export default function Footer() {
    return (
        <footer>
            <p>
                Made with
                <inline>
                    <Link to="https://react.dev/" target="_blank" rel="noopener noreferrer">
                        <img style={{height: "50px", marginLeft: "10px", transform: "translate(0, -10%)"}} src={reactLogo} alt="React" align="middle" />
                    </Link>
                    {/*<Link to="https://nodejs.org/" target="_blank" rel="noopener noreferrer">
                        <img style={{height: "50px", marginLeft: "10px"}} src={nodeLogo} alt="NodeJS" align="middle" />
                    </Link>*/}
                    <span style={{height: "0", marginRight: "10px"}}></span>
                </inline>
                for
                <inline>
                    <Link to="https://teardowngame.com/" target="_blank" rel="noopener noreferrer">
                        <img style={{height: "50px", marginLeft: "10px", transform: "translate(0, -10%)"}} src={teardownLogo} alt="Teardown" align="middle" />
                    </Link>
                </inline>
            </p>
        </footer>
    )
}