import React from "react"
import Func from "./Func"

export default function FuncCatgeory({ name, description, functions}) {
    const [windowWidth, setWindowWidth] = React.useState(window.innerWidth)
    const [screenWidth, setScreenWidth] = React.useState(window.screen.width)
    const screenWidthTest = window.screen.width
    const [maximized, setMaximized] = React.useState(false)
    const [descMaximized, setDescMaximized] = React.useState(false)
    const categoryClass = maximized ? "func--grid" : "func--flex"
    const maxColumns = 3
    const gridColumns = Math.min((Math.ceil((windowWidth - 400) / (screenWidthTest / maxColumns))), functions.length)

    //console.log(windowWidth)

    React.useEffect(() => {
        const handleResize = () => {
            setWindowWidth(window.innerWidth);
        };
    
        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const categoryStyle = {
        gridTemplateColumns: "1fr ".repeat(gridColumns)
    }

    const funcPrevs = functions.map(func => (
        <span className="func--prev">{func.name}</span>
    ))
    
    const funcs = functions.map(func => (
        <Func {...func} />
    ))

    const categoryFlex = (
        <div className="func--flex">
            {funcPrevs}
        </div>
    )

    const descStyle = {
        maxHeight: descMaximized ? "" : "100px"
    }

    const categoryGrid = (
        <div className="func--grid" style={categoryStyle}>
            {funcs}
        </div>
    )

    function toggleMaximized() {
        console.log(maximized)
        setMaximized(prevState => !prevState)
    }

    function toggleDescMaximized() {
        setDescMaximized(prevState => !prevState)
    }

    if (functions && functions.length > 0) {
        return(
            <div>
                <button
                    className="func--category-button"
                    onClick={() => toggleMaximized()}
                >
                    <h2 className="func--category-header">
                        {name}
                    </h2>
                </button>
                <p 
                    className="func--text func--category-description"
                    style={descStyle} 
                    onClick={() => toggleDescMaximized()}
                >
                    {description}
                </p>
                {categoryFlex}
                {maximized ? categoryGrid : ""}
                <hr />
            </div>
        )
    }

    return false
}